@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800&family=Roboto+Mono:wght@100;200;300&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

:root {
    /* ===Colours=== */
    --body-background: #ffff;
    --sidebar-color: whitesmoke;
    --primary-color: #695cfe;
    --primary-color-light: #f6f5ff;
    --toggle-color: #ddd;
    --text-color: #707070;
    --border-color: black;
    --activebg-color: #695cfe;
    --activet-color: #fff;
    --bar-color: #695cfe;

    /* ==== Transitions ==== */
    --tran-02: all 0.2s ease;
    --tran-03: all 0.3s ease;
    --tran-04: all 0.4s ease;
    --tran-05: all 0.5s ease;
}

/* ===BODY=== */
.App {
    background-color: #ffff;
    background-color: var(--body-background);
    transition: all 0.3s ease;
    transition: var(--tran-03);
}

/* ===BODY-DARK */
.App.dark {
    --body-background: #18191a;
    --sidebar-color: #242526;
    --primary-color: #3a3b3c;
    --primary-color-light: #3a3b3c;
    --toggle-color: #fff;
    --text-color: #ccc;
    --border-color: #ddd8d8f0;
    --activebg-color: whitesmoke;
    --activet-color: black;
    --bar-color: turquoise;


    .logo img {
        -webkit-filter: invert(0.9) !important;
                filter: invert(0.9) !important;
    }

    .sun {
        opacity: 1;
    }

    .moon {
        opacity: 0;
    }

    .contacts ul i {
        -webkit-filter: invert(1);
                filter: invert(1);
    }

    .menu button img {
        -webkit-filter: invert(1);
                filter: invert(1);
    }

    .homepage-bg {
        -webkit-filter: brightness(0.2) invert(0) blur(2px);
                filter: brightness(0.2) invert(0) blur(2px);
    }
}

/* scroller */
::-webkit-scrollbar-track {
    border-radius: 15px;
    background-color: transparent;
}

::-webkit-scrollbar {
    width: 8px;
    background-color: transparent;
}

::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
    background-color: #555;
}
html,
body {
  width: 100%;
  height: 100%;
}

.App {
  width: 100%;
  height: 100%;
  -webkit-transform-origin: top left;
          transform-origin: top left;
}

/* Button */
.button button{
    border-style: none;
    background: transparent;
}
/* Navbar */
.navbar{
    position: fixed;
    width: 100%;
    padding: 1rem;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 3px solid var(--border-color);
    background-color: var(--body-background);
    z-index: 100;
}
.logo{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30%;
}
.logo img{
    height: 2.5rem;
}
.title{
    color: var(--border-color);
    text-align: center;
    width: 30%;
    font-size: 2rem;
}
.menu{
    display: none;
    flex-direction:row-reverse;
    width: 30%;
}
.menu img{
    height: 2rem;
}

@media (max-width:968px) {
    .navbar{
        justify-content: space-around;
    }
    .title{
        display: none;
    }
    .menu{
        display: flex;
    }
    .sidebar{
        width: 0;
    }
    .main-body{
        width: 100%;
    }
}
@media (max-width:633px) {
    .navbar{
        padding: 0;
        height: 50px;
    }
    .logo img{
        margin-top: 5px;
        margin-left: 10px;
        height: 1.7rem;
    }
}
@media (max-width:390px){
    .logo img{
        margin-left: 5rem;
    }
}
/* SideBar */
.sidebar {
    background-color: var(--sidebar-color);
    position: fixed;
    height: 100vh;
    width: 250px;
    padding-top: 70px;
    right: 0;
    overflow-y: scroll;
    transition: width 0.4s ease-in-out;
    z-index: 99;
}

.sidebar.show {
    width: 250px;
}

.sidebar-content {
    padding: 1rem;
}

.content {
    margin-left: 0px;
}

.nav-link {
    padding-left: 10px;
}

a {
    text-decoration: none;
}

button {
    border-style: none;
    background: var(--sidebar-color);
    cursor: pointer;
}

.nav-link.active button {
    background-color: var(--activebg-color);
}

.nav-link.active button .icon,
.nav-link.active button .text {
    color: var(--activet-color);
}

.content li {
    height: 50px;
    list-style: none;
    display: flex;
    align-items: center;
}

.content li button,
.content li text {
    display: flex;
    align-items: center;
}

.content li .icon {
    min-width: 30px;
}

.content li .text {
    width: 100%;
}

span.text.nav-text {
    text-align: left;
}

.content li .text,
.content li .icon {
    margin-left: 10px;
    font-size: 18px;
    color: var(--text-color);
}

.content li button {
    height: 90%;
    width: 100%;
    display: flex;
    text-decoration: none;
    border-radius: 6px;
    transition: var(--tran-03);
}

.content li button:hover {
    background-color: var(--activebg-color);
}

.content li button:hover .icon,
.content li button:hover .text {
    color: var(--activet-color);
}

.content li button:active .icon,
.content li button:active .text {
    color: var(--sidebar-color);
}

.side-heading {
    width: 200px;
    margin: 15px 0;
    font-size: 1.3rem;
    font-weight: 600;
    color: var(--text-color);
}

/* Theme */
.sidebar .sidebar-content {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.moon-sun .icon {
    font-size: 25px !important;
}

.mode .moon-sun {
    height: 50px;
    width: 60px;
    display: flex;
    align-items: center;
}

.mode i {
    position: absolute;
}

.moon-sun .sun {
    opacity: 0;
    transition: var(--tran-03);
}

.toggle-switch {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    cursor: pointer;
    min-width: 60px;
}

.toggle-switch .switch {
    display: inline-block;
    height: 22px;
    width: 44px;
    border-radius: 25px;
    background-color: var(--toggle-color);
}

.switch::before {
    display: inline-block;
    content: '';
    height: 15px;
    width: 15px;
    border-radius: 50%;
    position: relative;
    left: 5px;
    background-color: var(--sidebar-color);
    transition: var(--tran-03);
}

/* Dark theme */
.App.dark .switch::before {
    left: 23px;
}

/* Contacts */
.contacts ul {
    display: flex;
    justify-content: space-around;
}

.contacts ul li button:hover {
    background-color: transparent;
}

.contacts ul i {
    font-size: 30px;
    color: black;
}

@media (max-width:968px) {
    .sidebar {
        width: 0;
    }
}

@media (max-width:633px) {

    .sidebar,
    .main-body {
        padding-top: 50px;
    }
}



.sound-dropdown {
    width: 100%;
    padding: 8px;
    margin-top: 10px;
    background-color: #f0f0f0;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.sound-dropdown.dark-mode {
    background-color: #333;
    color: #fff;
    border: 1px solid #555;
}

.sound-section {
    margin-bottom: 20px;
}
.mainpage{
    /* height: calc(100vh - 70px); */
    height: 100vh;
    width: calc(100% - 250px);
    padding-top: 70px;
    z-index: -2;
}
@media (max-width:968px) {
    .mainpage{
        width: 100%;
        padding-top: 70px;
    }
}
@media (max-width:633px) {
    .mainpage{
        padding-top: 50px;
    }
}


.sortingPage-main{
    height: 100%;
    width: 100%;
    background: var(--body-background);
}
.sort-settings{
    color: var(--border-color);
    height: 60px;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0 20px;
    background-color: var(--sidebar-color);
}
.sort-settings div{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 5px 20px;
    width: 40%;
}
.sort-settings div label{
    width: 33%;
}
.sort-settings div span{
    width: 10%
}
.array-container{
    display: flex;
    width: 100%;
    height: 70%;
    justify-content: center;
    
}
.array-bar-container{
    width: 80%;
    display: inline-block;
    margin:  0 auto;
    display: flex;
}
.array-bar{
    background-color: #695cfe;
    margin-left: 2px;
}
.controls{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 16%;
    width: 100%;
}
.sortingPage-main button{
    margin: 0 10px;
    padding: 10px 20px;
    height: 40px;
    border-radius: 5px;
    border: none;
    outline: none;
    cursor: pointer;
    font-size: 1rem;
    background-color: var(--sidebar-color);
    color: var(--text-color);
    transition: all 0.2s ease-in-out;
}
.sortingPage-main button:hover{
    background-color: var(--activebg-color);
    color: var(--sidebar-color);
}
.sort-info{
    height: 140px;
    display: flex;
    color: var(--text-color);
    background-color: var(--body-background);
    color: var(--text-color);
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

@media (max-width: 633px) {
    .sort-settings{
        font-size: 12px;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
    }
    .sort-settings div{
        width: 100%;
        margin: 1px 0;
    }
}
input[type=range]{
    margin: 0 20px;
    height: 2px;
    width: 50%;
}
@media (max-width: 473px){
    .controls{
        position: relative;
        top: 15px;
        flex-direction: column;
    }
    .controls button{
        width: 70%;
        margin: 5px 0;
    }
    .sort-info{
        height: 140px;
        display: flex;
        flex-direction: column;
    }
}

.homepage-main {
    height: 100%;

}

.page {
    height: 100%;
    width: 100%;
}

.page:not(:first-child) {
    position: relative;
    height: auto;
    background-color: transparent;
    color: var(--text-color);
    z-index: 2;
}

.homepage-bg {
    position: fixed;
    width: 100%;
    height: 100vh;
    background: url(/static/media/homepage_bg1.de9a2ee0.jpg);
    -webkit-filter: brightness(0.3) invert(1) blur(2px);
            filter: brightness(0.3) invert(1) blur(2px);
    background-size: cover;
    z-index: 0;
}

.scramble-container {
    position: relative;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, var(--body-background));
}

.scramble-text {
    color: var(--text-color);
    font-family: 'Roboto Mono', monospace;
    font-size: 60px;
    text-shadow: 1px 5px 5px var(--text-color);
}

.page-2 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    /* Auto-sized rows */
    background: var(--body-background);
}

.box {
    width: 100%;
    height: 100%;
}

.box.left {
    grid-column: 1;
    background: var(--body-background);
    height: 100%;
}

.time-complexity {
    height: 40%;
    background: var(--body-background);
}

.box.right {
    grid-column: 2;
    display: flex;
    flex-direction: column;
}

.ques-ans {
    padding: 20px;
}

section {
    margin: 20px;
}

p {
    margin: 20px 20px;
    opacity: 0;
    -webkit-transform: translateX(30%);
            transform: translateX(30%);
    transition: all 0.7s ease-in;
}

p.show {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.ques {
    display: inline-block;
    font-size: 2rem;
    color: #695cfe;
    opacity: 0;
    -webkit-transform: translateX(50%);
            transform: translateX(50%);
    transition: all 1s ease-in;
}

.sort-types-heading {
    display: flex;
    height: 20%;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    color: #695cfe;
    background: var(--body-background);
}

.sort-types {
    height: 500px;
    width: 100%;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    background: linear-gradient(to bottom, var(--body-background), transparent 50%, var(--body-background));
    z-index: 2;
}

.sort-types-container {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin: 0 1rem;
}

.ques.show {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.ans {
    margin: 20px 20px;
    width: 85%;
}

.sort-row {
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 99px;
}

.sort-type {
    font-family: 'Roboto Mono', monospace;
    font-size: 20px;
    color: var(--border-color);
    text-shadow: 0 5px 5px var(--text-color);
    letter-spacing: 1px;
    display: flex;
    align-items: center;
    height: 100%;
    width: 33%;
    font-weight: 500;
    z-index: -20;
}

.sort-row:nth-child(1n) .sort-type {
    justify-content: center;
}

.sort-row:nth-child(2n) .sort-type {
    justify-content: flex-start;
}

.sort-row:nth-child(3n) .sort-type {
    justify-content: flex-end;
}

.sort-row.infinite-scroll {
    -webkit-animation: scrollUp 10s linear infinite;
            animation: scrollUp 10s linear infinite;
}

.time-complexity .ques {
    display: inline-block;
    font-size: 2rem;
    color: #695cfe;
    opacity: 1;
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    transition: all 1s ease-in;
}

.time-complexity .ques.show {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.time-complexity p {
    font-size: 1rem;
    opacity: 0;
    -webkit-transform: translateY(20%);
            transform: translateY(20%);
    transition: all 1s ease-in;
}

.time-complexity p.show {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.page-3.page {
    border-top: 3px solid var(--border-color);
    ;
    height: 10%;
    background-color: var(--body-background);
    display: flex;
    justify-content: center;
    align-items: center;
    letter-spacing: 2px;
    font-size: 20px;
}

@-webkit-keyframes scrollUp {
    0% {
        z-index: -20;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }

    100% {
        z-index: -20;
        -webkit-transform: translateY(-600%);
                transform: translateY(-600%);
    }
}

@keyframes scrollUp {
    0% {
        z-index: -20;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }

    100% {
        z-index: -20;
        -webkit-transform: translateY(-600%);
                transform: translateY(-600%);
    }
}


@media (max-width:670px) {
    .scramble-text {
        font-size: 50px;
    }
}

@media (max-width:400px) {
    .scramble-text {
        font-size: 28px;
    }
}

@media (max-width:1365px) {

    .ques,
    .sort-types-heading {
        font-size: 1.5rem;
    }

    .time-complexity .ques {
        font-size: 1.9rem;
        margin: 10px 10px;
    }

    section {
        margin: 0;
    }

    p {
        margin: 10px 8px;
    }
}

@media (max-width:649px) {
    .page-2 {
        display: flex;
        flex-direction: column;
    }

    .ques-ans {
        overflow-y: hidden;
    }

    p {
        -webkit-transform: translateY(20%);
                transform: translateY(20%);
        transition: all 0.7s ease-in;
    }

    .ques {
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
        transition: all 1s ease-in;
    }

    .sort-types-heading {
        padding-bottom: 35px;
    }
}

@media (max-width:549px) {
    .scramble-text {
        font-size: 30px;
    }

    .sort-type {
        font-size: 18px;
    }
}

@media (max-width:380px) {
    .sort-type {
        font-size: 13px;
    }

    .page-3.page {
        font-size: 15px;
    }
}
